

@mixin advantages_01 {
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	@media screen and (min-width: 1180px) {
		background-attachment: fixed;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 50px 0;
	}
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		// background-color: rgba(black, 0.5);
		background-color: rgba(lighten($color_style, 40%), 0.7); //или 0.7
	}
	.block-content {
		color: #000;
		position: relative;
		z-index: 1;
	}
	h2{
		margin: 0 auto;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin: 0 0 10px;
			font-size: 30px;
			line-height: 1.1;
		}
	}
	.field--name-body {
		p {
			text-align: center;
			font-size: 20px;
			margin: 0 auto 50px;
			@media screen and (min-width: 768px) {
				width: 650px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 	16px;
				margin: 0 0 30px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 16px;
			}
		}
	}
	.field--type-field-collection {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;		
		&>.field__item {
			width: 25%;
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 48%;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				&:not(:last-child) {
					margin-bottom: 30px;					
				}	
			}
		}				
	}
	.content {
		box-sizing: border-box;
		padding: 0 20px;
		text-align: center;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 0 10px;	
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding: 0 5px;	
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 0;
			// text-align: left;	
		}
	}
	.field--name-field-piktogramma {
		width: 80px;
		height: 80px;
		margin: 0 auto 20px;
		color: lighten($color_style, 40%);
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 50px;
			height: 50px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			// float: left;
			// margin-right: 20px;	
			// margin-bottom: 0;
			width: 50px;
			height: 50px;
		}
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.field--name-field-zagolovok {
		margin-bottom: 5px;
		font-size: 28px;
		font-weight: 700;
		line-height: 1.1;
		font-family: $font-title;
		color: $color_style; //lighten($color_style, 40%);
		@media screen and (min-width: 480px) and (max-width: 767px) {
			font-size: 14px;
			line-height: 1.2;

		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			margin-bottom: 10px;
			color: #000;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 0;
			font-size: 18px;
			color: #000;
		}
	}
	.field--name-field-opisanie {
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 14px;
		}	
		@media screen and (min-width: 320px) and (max-width: 767px) {
			// font-size: 12px;	
			display: none;
		}
	}
	.field--name-field-izobrazenie {
		display: none;
	}
}

@mixin advantages_02 {
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	@media screen and (min-width: 1180px) {
		background-attachment: fixed;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 50px 0;
	}
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		// background-color: rgba(black, 0.5);
		// background-color: rgba(lighten($color_style, 40%), 0.5); //или 0.7
		background-color: rgba(lighten($color_style, 40%), 0.7); //или 0.7
	}
	.block-content {
		color: #000;
		position: relative;
		z-index: 1;
	}
	h2{
		margin: 0 auto;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			margin: 0 0 10px;
			font-size: 30px;
			line-height: 1.1;
		}
	}
	.field--name-body {
		p {
			text-align: center;
			font-size: 20px;
			margin: 0 auto 50px;
			@media screen and (min-width: 768px) {
				width: 650px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 	16px;
				margin: 0 0 30px;
			}
		}
	}
	.field--type-field-collection {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;		
		&>.field__item {
			width: 47%;
			margin-bottom: 80px;
			@media screen and (min-width: 1180px) {
				&:nth-child(3), &:nth-child(4) {
					margin-bottom: 0;
				}
			}
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				&:nth-child(3), &:nth-child(4) {
					margin-bottom: 0;
				}
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 46%;
				margin-bottom: 50px;
				&:nth-child(3), &:nth-child(4) {
					margin-bottom: 0;
				}
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				margin-bottom: 40px;
				&:nth-child(3), &:nth-child(4) {
					margin-bottom: 0;
				}
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 100%;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				&:not(:last-child) {
					margin-bottom: 30px;					
				}	
			}
		}				
	}
	.content {
		position: relative;
		padding-left: 130px;
		box-sizing: border-box;
		text-align: left;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding-left: 120px;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding-left: 100px;	
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding-left: 70px;	
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding-left: 90px;
			text-align: left;	
		}
	}
	.field--name-field-piktogramma {
		width: 100px;
		height: 100px;
		margin: 0 auto 20px;
		position: absolute;
		left: 0;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			width: 80px;
			height: 80px;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 50px;
			height: 50px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			float: left;
			margin-right: 20px;	
			margin-bottom: 0;
			width: 60px;
			height: 60px;
		}
		img {
			width: auto;
			height: auto;
			display: block;
			max-width: 100%;
			max-height: 100%;
		}
	}
	.field--name-field-zagolovok {
		margin-bottom: 5px;
		font-size: 36px;
		font-weight: 700;
		line-height: 1;
		font-family: $font-title;
		color: $color_style; //lighten($color_style, 40%);
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			font-size: 32px;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 22px;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			font-size: 14px;
			line-height: 1.2;

		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 0;
			font-size: 22px;
		}
	}
	.field--name-field-opisanie {
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 14px;
		}	
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 12px;	
		}
	}
	.field--name-field-izobrazenie {
		display: none;
	}
}