//Меню в сайдбаре
@mixin menu_sidebar_01 {
	ul {
		list-style-type: none;
	}
	h2 {
		box-sizing: border-box;
		background-color: $color_style;
		color: #fff;
		padding: 15px 20px;
		line-height: 1;
		font-size: 24px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 18px;
		}
	}
	.menu {
	}
	.menu-item {
		margin: 5px 0;
		display: table;
		width: 100%;
		a {
			display: table-cell;
			background-color: $color_bg;
			text-decoration: none;
			color: inherit;
			padding: 0px 20px 0px 10px;
			height: 70px;
			border-left: 10px solid transparent;
			font-size: 16px;
			font-weight: 700;
			width: 100%;
			line-height: 1.2;
			box-sizing: border-box;
			vertical-align: middle;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				height: 60px;
				font-size: 14px;
				border-width: 5px;
			}
			&:hover, &:focus, &.is-active {
				background-color: lighten($color_style, 30%);
				border-color: $color_style;
			}
		}
	}
	
}