
/* Вариант 1 */
@mixin wrap_01{
	@at-root body:not(.path-frontpage) header {
		background-image: url(../images/header_bg_01.jpg);
		// background-size: cover;
		background-repeat: repeat;
		background-color: lighten($color_style, 50%);
		width: 100%;
		border-bottom: 2px solid #232323;
		box-shadow: 0 1px 2px -2px #000;

		@media screen and (max-width: 767px) {
			width: 100%;
			padding: 5px 0;
			box-shadow: 0 2px 10px 3px rgba(0,0,0,0.5);
			& ~ main {
				margin-top: 35px;
				padding-top: 10px;
			}
		}
		.region-header{
			display: flex;
			flex-flow: row wrap;
			justify-content: space-between;
			position: relative;
			width: 1140px;
			margin: 0 auto;
			padding: 10px 0 0;
			@media screen and (min-width: 768px) and (max-width: 1179px) {
				width: auto;
			}
			@media screen and (max-width: 767px) {
				width: 100%;
				position: fixed;
				padding: 5px 0;
			}
		}
	}

	@media screen and (min-width: 320px) and (max-width: 767px) {
		background-color: #fff;
		padding: 5px 0;
		border-bottom: 2px solid;
		position: fixed;
		width: 100%;
		z-index: 999;
		top: 0;
		.responsive-menu-toggle{
			position: absolute;
		}
	}
}
@mixin contact_01{
	@at-root body:not(.path-frontpage) & {
		position: relative;
		order: 2;
		width: 50%;
		float: right;
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			padding: 0 20px 0 0;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			.telephone{
				font-size: 18px;
			}
		}
		@media screen and (max-width: 767px) {
			// display: none;
		}
		.field--name-body{
			padding: 0;
			width: auto;
			margin: 0;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 14px;
			}
		}
		.telephone{
			line-height: 1.1;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 18px;
			}
		}
		.soc-seti{
			a{
				width: 35px;
				height: 35px;
			}
		}
	}

	width: 100%;
	position: absolute;
	@media screen and (min-width: 480px) and (max-width: 767px) {
		width: auto !important;
		position: absolute !important;
		right: 10px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		display: none;
	}
	.field--name-body{
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			display: block;
			text-align: right;
			padding: 0;
		}
		@extend %layout;
		padding-top: 10px;
	}
	.contact{
		.telephone{
			font-family: $font_title;
			font-weight: 700;
			margin: 0;
			font-size: 24px;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 16px;
				line-height: 1.3;
			}
		}
		.e-mail{
			margin: 0;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 12px;
				line-height: 1.3;
			}
			a{
				color: #000;
			}
		}
	}
	.soc-seti{
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			display: none;
		}
		a{
			margin: 0 0 0 5px;
			width: 40px;
			height: 40px;
			text-indent: -9999px;
			background-size: 100% auto;
			background-repeat: no-repeat;
			background-position: 0 0;
			&:hover{
				background-position: 0 bottom;
			}
		}
	}
}
@mixin brand_01{
	@at-root body:not(.path-frontpage) & {
		position: relative;
		top: inherit;
		order: 1;
		width: 40%;
		margin: 0;
		text-align: left;
		@media screen and (max-width: 767px) {
			width: 100%;
			box-sizing: border-box;
		}
		.logo{
			float: left;
			margin: 0 20px 0 0;
			padding: 0;
			display: inline-block;
			&.logo--white {
				display: none;
			}
			&::before {
				content: none;
			}
		}
		img {
			max-width: 150px;
			max-height: 50px;
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				max-width: 90px;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				max-width: 80px;
			}
			@media screen and (min-width: 320px) and (max-width: 767px) {
				max-height: 35px;
				max-width: 40px;
			}
		}
		.site-name{
			font-size: 24px;
			letter-spacing: 0.06em;
			text-align: left;
			line-height: 1.1;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 18px;
			}
			@media screen and (max-width: 767px) {
				font-size: 18px;
			}
		}
		.site-slogan{
			text-align: left;
			font-size: 16px;
			text-transform: none;
			letter-spacing: 0;
			font-weight: normal;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 12px;
			}
			@media screen and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}

	@extend %layout;
	text-align: center;
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 0 0 0 60px !important;
		text-align: left;
	}
	.logo{		
		display: block;
		padding: 20px 0 10px;
		position: relative;
		vertical-align: middle;
		z-index: 1;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			border: none;
			padding: 0;
			float: left;
			margin-right: 15px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-right: 12px;
		}
		&.logo--white {
			display: none;
		}
		&::before {
			content: '';
			display: inline-block;
			width: 100px;
			height: 10px;
			background-color: #000;
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: -50px;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				display: none;
			}
		}
		a {
			display: block;
			font-size: 0;
		}
	}
	img{
		display: inline-block;	
		width: auto;
		height: auto;
		max-width: 250px;
		max-height: 60px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			max-width: 70px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			max-width: 60px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			max-height: 35px;
		}
	}
	#name-and-slogan {
		display: inline-block;
		vertical-align: middle;
	}
	.site-name{
		text-transform: uppercase;
		letter-spacing: 2px;
		font-family: $font_title;
		font-weight: 900;
		font-size: 40px;
		margin: 0;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 16px;
			line-height: 1.3;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			letter-spacing: 1px;
		}
		a{
			text-decoration: none;
			color: #000;
		}
	}
	.site-slogan{
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 700;
		margin: 0;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			text-transform: inherit;
			letter-spacing: 0;
			font-weight: normal;
			font-size: 12px;
			line-height: 1.3;
		}
	}
}
@mixin menu_01{
	@at-root body:not(.path-frontpage) & {
		position: relative;
		top: inherit;
		order: 3;
		width: 100%;
		margin-top: 30px;
		margin-bottom: 10px;
		// margin: 30px auto 10px !important;
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			padding: 0;
		}
		&.animated-menu{
			top: 0;
			left: 0;
			padding: 10px 0;
			position: fixed;
			border-bottom: 2px solid #000;
			box-shadow: 0 0px 15px 7px rgba(0, 0, 0, 0.3);
			z-index: 999;
			width: 100%;
			background-color: #fff;
			margin-top: 0;
			margin-bottom: 0;
			ul{
				justify-content: center;
			}
			// top: 0;
			// left: 0;
			// padding: 10px 0;
			// position: fixed;
			// z-index: 999;
			// border-bottom: 2px solid #000;
			// box-shadow: 0 0px 15px 7px rgba(0,0,0,0.3);
			ul{
				// justify-content: center;
			}
		}
		ul{
			justify-content: left;
			li{
				border: none;
				flex: 1;
			}
			a{
				padding: 0;
				&:hover{					
					color: $color_style;
				}
				&.is-active{
					color: $color_style;
				}
			}
		}
	}

	@extend %layout;
	// margin: 30px auto 30px !important;
	margin-top: 30px;
	margin-bottom: 30px;
	@media screen and (max-width: 767px) {
		display: none;
	}
	.block-content {
		@extend %layout;
	}
	&.animated-menu{
		top: 0;
		left: 0;
		padding: 10px 0;
		position: fixed;
		border-bottom: 2px solid #000;
		box-shadow: 0 0px 15px 7px rgba(0, 0, 0, 0.3);
		z-index: 999;
		width: 100%;
		background-color: #fff;
		margin-top: 0;
		margin-bottom: 0;
		// margin-top: 0 !important;
		// margin-bottom: 0 !important;
		ul{
			justify-content: center;
		}
		li {
			&:not(:last-child) {
				border-right: 2px solid #ccc;
			}
		}
	}
	ul{
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;
		li{
			float: none;
			text-transform: uppercase;
			letter-spacing: 1px;
			font-weight: 700;
			font-family: $font_title;
			font-size: 18px;
			border-right: 2px solid #ccc;
			flex-grow: 0.1;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 25%;
				text-align: center;
				font-size: 16px;
			}
			&:last-child{
				border: none;
			}
			a{
				text-decoration: none;
				padding: 0;
				color: #000;
				text-align: center;
				line-height: 50px;
				@media screen and (min-width: 768px) and (max-width: 979px) {
				}
				&:hover{					
					color: $color_style;
				}
				&.is-active{
					color: $color_style;
				}
			}
			&.menu-item--expanded{
				overflow: hidden;
				&:hover {
					overflow: inherit;
					> ul{
						opacity: 1;
						margin: 0;
						border-top: 4px solid #232323;
					}
				}
				> a::after{
					content: '';
					display: block;
					width: 10px;
					height: 10px;
					position: absolute;
					bottom: 2px;
					left: calc(50% - 5px);
					background-image: url(../images/arrow-bottom.png);
					background-repeat: no-repeat;
					background-size: 100%;
					background-position: center;
				}
			}
		}
		&.sub-nav{
			background-color: #fff;
			box-sizing: border-box;
			flex-flow: column;
			box-shadow: 0 4px 5px -2px rgba(0,0,0,0.3);
			border-top: none;
			transition: 0.2s;
			opacity: 0;
			margin-top: -20px;
			li{
				display: block;
				width: 100%;
				border: none;
				letter-spacing: 0;
				text-transform: inherit;
				border-bottom: 1px solid #ccc;
				text-align: left;
				a{
					text-align: left;
					padding: 0 10px;
				}
			}
		}
	}
}
@mixin picture_01($titr-logo-color: black) {
	background-color: $color_style;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	// border-bottom: 10px solid $color_style;
	@media screen and (max-width: 1179px) {
		background-attachment: scroll;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		margin-top: 45px;
	}
	.titr_logo {
		display: none;
	}
	.titr_logo--#{$titr-logo-color} {
		display: block;
	}
	.logo_image {
		width: auto;
		height: auto;
		max-width: 200px;
		max-height: 125px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			max-width: 150px;
			max-height: 95px;
		}
	}
	.field--type-image{
		display: none;
	}
	.field--name-body{
		@extend %layout;
		padding: 100px 0;
		text-align: center;
		// height: 300px;
		color: #fff;
		line-height: 1.7;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 50px 0;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 50px 0;
			// height: 270px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 30px 0;
			height: auto;
			width: 460px;
			margin: 0 auto;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 50px 0;
			height: auto;
			width: 300px;
			margin: 0 auto;
		}
	}
	h1{
		font-family: $font_title;
		font-size: 80px;
		font-weight: bold;
		letter-spacing: 2px;
		line-height: 80px;
		padding: 0 150px 0px;
		text-align: center;
		text-shadow: 1px 1px 2px #444;
		text-transform: uppercase;
		margin-bottom: 20px;
		margin-top: 20px;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			font-size: 70px;
			line-height: 1.1;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 60px;
			line-height: 1.1;
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 44px;
			line-height: 1.1;
			padding: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 30px;
			line-height: 1.3;
			padding: 0;
		}
	}
	p{
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 700;
		text-shadow: 1px 1px 2px #444;
		width: 680px;
		margin: 0 auto;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 0;
			font-size: 16px;
			width: auto;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 0;
			font-size: 16px;
		}
	}
}
@mixin brand_anim_01 {
	display: none;
}
