		.menu-item {
			width: 100%;
			border: none;
			border-top: 1px solid rgba(0,0,0,0.2);
			>a {
				padding: 0 20px;
				line-height: 50px;
				width: 100%;
				&:hover, &:focus {
					color: $color_style;
				}
			}
		}
	}	
}
@mixin picture_04($titr-logo-color: black) {
	background-color: $color_style;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;	
	padding: 100px 0 120px;
	// border-bottom: 10px solid $color_style;
	@media screen and (min-width: 1180px) {
		background-attachment: fixed;
	}
	@media screen and (min-width: 980px) and (max-width: 1179px) {
		padding: 70px 0 140px;
	}
	@media screen and (min-width: 768px) and (max-width: 979px) {
		padding: 20px 0 0px;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		margin-top: 45px;
		padding: 0;
	}
	.titr_logo {
		display: none;
	}
	.titr_logo--#{$titr-logo-color} {
		display: block;
	}
	.logo_image {
		width: auto;
		height: auto;
		max-width: 200px;
		max-height: 125px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			max-width: 150px;
			max-height: 95px;
		}
	}
	.field--type-image{
		display: none;
	}
	.field--name-body{
		@extend %layout;
		text-align: center;
		color: #fff;
		line-height: 1.7;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 0;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 50px 0;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding: 30px 0;
			width: 460px;
			margin: 0 auto;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 50px 0;
			width: 300px;
			margin: 0 auto;
		}
	}
	h1{
		font-family: $font_title;
		font-size: 70px;
		font-weight: bold;
		letter-spacing: 2px;
		line-height: 80px;
		padding: 0 150px 0px;
		text-align: center;
		text-shadow: 1px 1px 2px #444;
		text-transform: uppercase;
		margin-bottom: 20px;
		margin-top: 20px;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			font-size: 60px;
			line-height: 1.1;
			padding: 0;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 60px;
			line-height: 1.1;
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 44px;
			line-height: 1.1;
			padding: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 30px;
			line-height: 1.3;
			padding: 0;
		}
	}
	p{
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 700;
		padding: 0 50px;
		text-shadow: 1px 1px 2px #444;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding: 0 100px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 0;
			font-size: 16px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding: 0;
			font-size: 16px;
		}
	}
}
