
@mixin thanks_style_01 {
	.view {
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-row {
		width: 23.2%;
	}	
	.views-field-field-img {
		.field-content {
		}
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.more-link {
		margin-top: 50px;
		text-align: center;
		a {
			@extend %button_big;
		}
	}
}

@mixin thanks_internal_style_01 {
	.view {
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;
		margin-bottom: 100px;
	}
	.views-row {
		margin-bottom: 1vw;
		@media screen and (min-width: 1180px) {
			width: 13.428571%;
			margin-bottom: 10px;
			&:not(:nth-child(7n)) {
				margin-right: 1%;
			}
		}
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			width: 15.833%;
			&:not(:nth-child(6n)) {
				margin-right: 1%;
			}
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 19.2%;
			&:not(:nth-child(5n)) {
				margin-right: 1%;
			}
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 32.666%;
			&:not(:nth-child(3n)) {
				margin-right: 1%;
			}
		}
	}	
	.views-field-field-img {
		.field-content {
		}
		img {
			width: auto;
			height: auto;
			max-width: 100%;
			max-height: 100%;
			display: block;
			margin: 0 auto;
		}
	}
	.more-link {
		margin-top: 50px;
		text-align: center;
		a {
			@extend %button_big;
		}
	}
}

