$color_style: #F67800;

%ucase {
	text-transform: uppercase;
	letter-spacing: 0.06em;
}


@mixin card_style_01 { //главная
	.view { 
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: center;
		margin-bottom: 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			// text-align: left;
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 10px;
		}

	}
	.views-row {
		// width: 33.333%;
		width: 27%;
		box-sizing: border-box;
		// padding: 0 20px;
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			width: 30%;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			padding: 0;
			width: 30%;
			position: relative;
			// margin-bottom: 30px;
			min-height: 170px;
			display: flex;
			flex-flow: column nowrap;
			justify-content: center;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 100%;
			padding: 0;
			margin-bottom: 50px;
		}
		
	}
	.views-field-field-image {
		margin-bottom: 30px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			// position: absolute;
			// top: 0;
			// left: 0;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 15px;
		}
		.field-content {
			overflow: hidden;
			margin: 0 auto;
			width: 250px;
			height: 250px;
			border-radius: 50%;
			box-shadow: 3px 3px 5px 0 #444;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 25vw;
				height: 25vw;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				width: 100px;
				height: 100px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 170px;
				height: 170px;
			}
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.views-field-title {
		line-height: 1;
		.field-content {
			a {
				@extend %card_title;
				color: inherit;
				text-decoration: none;
				border-bottom: 1px solid;
				font-family: $font_title;
				font-size: 28px;
				&:hover, &:focus {
					border-color: transparent;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 18px;				
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 16px;
				}
			}
		}
	}
	.views-field-body {
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				display: none;
			}
		}
	}
	.views-field-field-cena {
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 18px;
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}
}

@mixin card_style_02 { //внутри
	@extend %layout;
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: left;
		margin-bottom: 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			text-align: left;
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 10px;
		}
	}
	.views-row {
		width: 100%;
		box-sizing: border-box;
		position: relative;
		min-height: 300px;
		margin-bottom: 50px;
		border-bottom: 1px solid #ddd;
		@media screen and (min-width: 1180px) {
			padding-left: 400px;			
		}
		@media screen and (min-width: 980px) and (max-width: 1179px) {
			padding-left: 22vw;
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding-left: 23vw;
			min-height: unset;
		}
	}
	.views-field-field-image {
		margin-bottom: 30px;
		position: absolute;
		top: 0;
		left: 0;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			position: static;
		}
		.field-content {
			overflow: hidden;
			margin: 0 auto;
			width: 350px;
			height: 220px;
			box-sizing: border-box;
			border: 5px solid #fff;
			box-shadow: 3px 3px 5px 0 #444;
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				width: 20vw;
				height: 14vw;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 20vw;
				height: 14vw;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				width: 350px;
				height: 250px;
				margin: 0;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 94vw;
				height: 67vw;
				margin: 0;
			}
			img {
				width: 100%;
				height: auto;
			}
		}
	}
	.views-field-title {
		line-height: 1;
		.field-content {
			a {
				@extend %card_title;
				color: inherit;
				text-decoration: none;
				border-bottom: 1px solid;
				font-family: $font_title;
				font-size: 28px;		
				&:hover, &:focus {
					border-color: transparent;
				}		
				@media screen and (min-width: 980px) and (max-width: 1179px) {
					font-size: 24px;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 20px;				
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 18px;
				}
			}
		}
	}
	.views-field-body {
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
		}
	}
	.views-field-field-cena {
		margin-bottom: 40px;
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}

}

@mixin card_style_03 { //главная
	.view { 
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: left;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			text-align: center;
		}
		&:not(.views-field-nid) {
			margin-bottom: 20px;
			@media screen and (min-width: 480px) and (max-width: 767px) {
				margin-bottom: 10px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				margin-bottom: 10px;
			}
		}
		&:not(.views-field-field-image):not(.views-field-nid) {
			padding: 0 20px;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				padding: 0 15px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				padding: 0 10px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				padding: 0 15px;
			}
		}
	}
	.views-row {
		width: 32%;
		box-sizing: border-box;
		background-color: $color_bg;
		transition: all 0.2s ease 0s;
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 100%;
			padding: 0;
			margin-bottom: 50px;
		}
		&:hover {
			box-shadow: 0 0 10px 2px #999;
		}		
	}
	.views-field-field-image {
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 20px !important;
		}
		.field-content {
			overflow: hidden;
			margin: 0 auto;
			width: 100%;
			height: 230px;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				height: 20vw;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				height: 20vw;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				height: 60vw;
			}
			img {
				width: 100%;
				height: auto;
			}
		}
	}
	.views-field-title {
		line-height: 1;
		.field-content {
			a {
				@extend %card_title;
				color: inherit;
				text-decoration: none;
				font-family: $font_title;
				font-size: 28px;
				&:hover, &:focus {
					color: $color_style;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 16px;				
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 20px;
				}
			}
		}
	}
	.views-field-body {
		@media screen and (min-width: 480px) and (max-width: 767px) {
			display: none;
		}
		&~.views-field-nid {
			flex-grow: 2;
		}
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}
	.views-field-field-cena {
		&:last-child {
			margin-bottom: 50px;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				margin-bottom: 40px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				margin-bottom: 30px;
			}
		}
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 18px;
			}
		}
	}
	.views-field-nid {

	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}
}

