/*Breadcrumbs*/
@mixin breadcrumbs_01 {
	.breadcrumb {
		@extend %layout;
		// margin-top: 20px;
		box-sizing: border-box;
		ol {
			margin: 0;
			padding: 0;
			list-style-type: none;
		}
		li {
			display: inline-block;
			margin-right: 5px;
			color: #999;
		}
		li:not(:first-child) {
			&::before {
				content: '»';
				display: inline-block;
				margin-right: 5px;
			}
		}
		a {
			color: inherit;
			font-size: 16px;
			&:hover, &:focus {
				color: #000;
				text-decoration: none;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}
}
/*end of breadcrumbs*/

/*Partners*/
@mixin partners_style_01 {
	.field--type-field-collection {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		&>.field__item {
			box-sizing: border-box;
			padding: 0 20px;
			width: 20%;
			margin-bottom: 40px;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				padding: 0 10px;
				width: 33.333%;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				margin-bottom: 20px;
			}
		}
	}	
	.field--name-field-piktogramma {
		height: 170px;
		margin-bottom: 20px;
		text-align: center;
		@media screen and (min-width: 320px) and (max-width: 1179px) {
			height: 15vw;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 5px;
		}
		&::before {
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
		}
		img {
			display: inline-bblock;
			width: auto;
			height: auto;
			max-width: 90%;
			max-height: 90%;
			vertical-align: middle;			
		}
	}
	.field--name-field-zagolovok {
		text-align: center;
		font-size: 20px;
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			font-size: 16px;
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			font-size: 14px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 12px;
		}
	}
}
/*end of partners*/


//=====================================
//=Contacts
//=====================================
@mixin contacts_style_01 {
	.region-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
	}
	#block-classier-breadcrumbs {
		width: 100%;
	}
	#block-classier-page-title {
		width: 100%;
	}
	#block-classier-content {
		box-sizing: border-box;		
		margin-bottom: 90px;
		@media screen and (min-width: 1180px) {
			width: 570px;
		}
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			width: calc(50% - 20px);
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			width: 100%;
			padding: 0 10px;
			box-sizing: border-box;
		}
		.block-content {
			padding-right: 50px;
		}
		p {
			strong {
				margin-right: 5px;
			}
			a {
				text-decoration: underline;
				color: $color_style;
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
	#block-classier-form {
		box-sizing: border-box;
		margin-bottom: 90px !important;
		@media screen and (min-width: 1180px) {
			width: 570px;
			margin: 0;
		}	
		@media screen and (min-width: 768px) and (max-width: 1179px) {
			width: calc(50% - 20px);
			margin: 0;			
		}
		@media screen and (min-width: 768px) and (max-width: 979px) {
			padding: 30px 30px;
		}
		h2 {
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				font-size: 32px;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 30px;
			}
		}
	}
	#block-1 {
		width: 100%;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			height: 250px;
		}
		.field--name-body {
			>ymaps {
				@media screen and (min-width: 320px) and (max-width: 767px) {
					height: 250px !important;
				}
			}
		}
	}
}
//=====================================
//=End of Contacts
//=====================================


