p,h1, h2, h3, h4, h5, ul, ol, li {
	margin: 0;
	padding: 0;
}

body {
	margin: 0;
	font-size: 16px;
	color: #000;
	line-height: 1.5;
	font-family: $font_text;
	background-color: #232323;
}
#mm-page{
	background-color: #fff;
}
.clearfix::after{
	display: none;
}


%title_h1{
	font-family: $font_title;
	font-size: 40px;
	line-height: 1.2;
	margin-top: 0;
	margin-bottom: 30px;
	@media screen and (min-width: 768px) and (max-width: 979px) {
		font-size: 36px;
	}
	@media screen and (min-width: 480px) and (max-width: 767px) {
		font-size: 32px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		font-size: 28px;
	}
}
%title_h2{		
	font-size: 40px;
	text-transform: uppercase;
	font-family: $font_title;
	letter-spacing: 0.06em;
	@media screen and (min-width: 320px) and (max-width: 767px) {
		font-size: 30px;
	}
}
%card_title {
	font-weight: 700;;
	text-transform: none;
}
%block {
	padding: 70px 0 100px;
	@media screen and (min-width: 768px) and (max-width: 979px) {
		padding: 50px 0 70px;
	}
	@media screen and (min-width: 480px) and (max-width: 767px) {
		padding: 50px 0;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		padding: 30px 0 50px;
	}
}

//=====================================
//=Основная разметка (контент и сайдбары)
//=====================================
body:not(.path-frontpage) main{
	display: flex;
	justify-content: center;
}
main {	
	width: 100%;
}
.layout-content {
	width: 100%;
}
.layout-content:not(:last-child) {
	width: 1px;
	order: 2;
	@media screen and (min-width: 1180px) {
		width: 820px;
	}
	@media screen and (min-width: 768px) {
		margin-right: 20px;		
	}
	@media screen and (min-width: 320px) and (max-width: 1179px) {
		flex-grow: 1;
	}
 	h1, .node__content, .view, .breadcrumb { //отменяем действие %layout 		
 		@media screen and (min-width: 768px) {
 			width: 100% !important;
 			box-sizing: border-box;
 		}
 	}
 	.view, h1, .breadcrumb, .node__content { //отменяем действие %layout
 		@media screen and (min-width: 1180px) {
 			padding-right: 0 !important;
 		}
 		@media screen and (min-width: 768px) and (max-width: 1179px) {
 			padding-left: 0 !important;
 			padding-right: 0 !important;
 		}
 	}
 	.node--type-tovar-usluga {
 		width: auto;
 	}
 	.node--type-tovar-usluga .field--type-image {
 		left: 0;
 		@media screen and (min-width: 768px) and (max-width: 979px) {
 			width: 100%;
 			position: static;
 			margin-bottom: 20px;
 		}
 	}
 	.node--type-tovar-usluga .node__content {
 		@media screen and (min-width: 768px) and (max-width: 979px) {
 			padding-right: 0; 			
 		}
 		@media screen and (min-width: 768px) and (max-width: 979px) {
 			padding-left: 0;
 			padding-right: 0;
 		}
 	}
 }
 .layout-sidebar-first {
 	width: 280px;
 	margin-right: 30px;
 	margin-bottom: 60px;
 	order: 1;
 	@media screen and (min-width: 320px) and (max-width: 767px) {
 		display: none;
 	}
 	@media screen and (min-width: 768px) and (max-width: 1179px) {
 		margin-left: 20px;
 	}
 	@media screen and (min-width: 980px) and (max-width: 1179px) {
 		width: 250px;
 	}
 	@media screen and (min-width: 768px) and (max-width: 979px) {
 		width: 200px;
 	}
 }
//=====================================
//=End of Основная разметка (контент и сайдбары)
//=====================================


