}
/* конец ссылки */

/* Меню */
.responsive-menu-toggle {
	@media screen and (min-width: 768px) {
		display: none;
	}
	.toggle::before{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 8px;
	}
	span.icon{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 16px;
	}
	span.label{
		display: none;
	}
	.toggle::after{
		background: #000;
		width: 30px;
		height: 4px;
		left: 10px;
		border-radius: 0px;
		top: 24px;
	}
}
.mm-opening .responsive-menu-toggle-icon{
	&::before, &::after{ top: 16px !important;}
}
/* конец меню */

