/* Кнопка */
%button_big {
	display: inline-block;
	text-decoration: none;
	color: #fff;
	font-size: 20px;
	font-weight: 300;
	line-height: 1;
	padding: 15px 50px;
	background-color: $color_style;
	box-shadow: 0px 2px 3px -1px #444;
	font-family: $font_title;
	text-transform: uppercase;
	letter-spacing: 0.06em;
	border-bottom: 4px solid darken($color_style, 10%);
	border-left: none;
	border-right: none;
	border-top: none;
	border-radius: 5px;
	&:hover, &:focus {
		background-color: darken($color_style, 5%);
	}
	@media screen and (min-width: 768px) and (max-width: 979px) {
		font-size: 18px;
	}
	@media screen and (min-width: 480px) and (max-width: 767px) {
		font-size: 16px;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		font-size: 14px;
		padding: 10px 30px;
	}
}
/* конец кнопка */

/* Контекстное меню */
.contextual-region .contextual {
	&.open{
		ul.contextual-links{
			display: block !important;
		}
	}
	ul.contextual-links{
		display: none !important;
		li a{
			color: #333 !important;
			padding: 0.4em 0.6em !important;
		}
	}
}
/* конец контекстное меню */

