@mixin blog_style_01 { //главная
	@at-root body:not(.path-frontpage) & {
		.view {
			@extend %layout;
		}
	}

	.view { 
		// @extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: left;
		&:not(.views-field-field-img):not(.views-field-nid) {
			padding: 0 20px;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				padding: 0 15px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				padding: 0 10px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				padding: 0 15px;
			}
		}
		@media screen and (min-width: 480px) and (max-width: 767px) {
			text-align: left;			
		}
	}
	.views-row {
		width: 32%;
		box-sizing: border-box;
		background-color: $color_style_bg;
		transition: all 0.2s ease 0s;
		position: relative;
		padding-bottom: 70px;
		margin-bottom: 30px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 32%;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 48%;
			&:not(:last-child) {
				margin-bottom: 5vw;				
			}
		}
		&:hover {
			box-shadow: 0 0 10px 2px #999;
		}		
	}
	.views-field-field-img {
		margin-bottom: 20px;
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 20px !important;
		}
		.field-content {
			overflow: hidden;
			margin: 0 auto;
			width: 100%;
			height: 230px;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				height: 20vw;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				height: 20vw;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				height: 30vw;
			}
			img {
				width: 100%;
				height: auto;
			}
		}
	}
	.views-field-title {
		margin-bottom: 10px;
		line-height: 1;
		.field-content {
			a {
				@extend %card_title;
				color: inherit;
				text-decoration: none;
				font-family: $font_title;
				font-size: 28px;
				&:hover, &:focus {
					color: $color_style;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 16px;				
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 14px;
				}
			}
		}
	}
	.views-field-created {
		margin-bottom: 10px;
		.field-content {
			color: #444;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}
	.views-field-body {
		margin-bottom: 20px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			display: none;
		}
		&~.views-field-nid {
			flex-grow: 2;
		}
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 12px;
			}
		}
	}
	.views-field-field-tags {
		margin-bottom: 20px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			font-size: 14px;			
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			// font-size: 12px;
			display: none;
		}
		.views-label {
			color: #444;
			display: inline-block;
			vertical-align: middle;
		}
		.field-content {
			color: #444;
			display: inline-block;
			text-transform: lowercase;
			vertical-align: middle;
		}
		a {
			color: inherit;
			&:hover, &:focus {
				color: $color-style;
				text-decoration: none;
			}
		}
	}
	.views-field-nid {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		.field-content {
			display: block;
			a {
				display: block;
				text-align: center;
				line-height: 50px;
				color: #fff;
				background-color: $color_style;
				text-decoration: none;
				font-family: $font_title;
				font-size: 20px;
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 18px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 16px;
					line-height: 40px;
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 14px;
					line-height: 40px;
				}
				&:hover, &:focus {
					opacity: .8;
				}
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-top: 10px;
		}
		a {
			@extend %button_big;
		}
	}
}
