
@mixin page_style_01 {
	.node__content {
		@extend %layout;
		margin-bottom: 50px;
	}
	.field--type-image {
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;

		@media screen and (min-width: 480px) and (max-width: 767px) {
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
		}
		.field__item {  // если изображений много
			width: 19.2%;
			margin-bottom: 1vw;
			box-sizing: border-box;
			border: 1px solid #ddd; 
			transition: all 0.2s ease 0s;
			&:hover, &:focus {
				transform: scale(1.1);
			}			
			@media screen and (min-width: 1180px) {
				margin-bottom: 12px;
			}
			@media screen and (min-width: 480px) {
				&:not(:nth-child(5n)) {
					margin-right: 1%;
				}
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 32.666%;
				&:not(:nth-child(3n)) {
					margin-right: 1%;
				}
			}
		}
		.field__item:first-child:last-child { //если изображение одно
			float: right;
			width: auto;
			display: block;
			margin: 0px 0 20px;
			max-width: 60%;
			&:hover, &:focus {
				transform: scale(1);
				box-shadow: 4px 4px 5px 0px #999;
			}
			@media screen and (min-width: 320px) and (max-width: 767px) {
				max-width: 460px;
			}
		}
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.field--name-body {
		@media screen and (min-width: 320px) and (max-width: 767px) {
			font-size: 14px;
		}
		ul,ol {
			padding-left: 30px;
		}
	}
}



