@mixin reviews_style_02 {
  background-repeat: repeat;
  background-blend-mode: overlay;
  background-image: url(../images/pattern.png) !important;
  @media screen and (min-width: 1180px) {
    background-attachment: fixed;
  }
  p {
    margin: 0;
  }
  .view {
  }
  .view-content {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
  .views-row {
    transition: all 0.2s ease 0s;
    width: 32%;
    box-sizing: border-box;
    background-color: #fff;
    padding: 30px 20px 50px;
    border-radius: 3px;
    @media screen and (min-width: 768px) and (max-width: 979px) {
      padding-left: 15px;
      padding-right: 15px;
      width: 32%; 
    }
    @media screen and (min-width: 480px) and (max-width: 767px) {
      width: 49%;
      margin-bottom: 2vw;
    }
    @media screen and (min-width: 320px) and (max-width: 479px) {
      width: 100%;
    }
    @media screen and (min-width: 320px) and (max-width: 479px) {
      &:not(:last-child) {
        margin-bottom: 30px;        
      }
    }
    &:hover {
      box-shadow: 0 0 10px 5px #999;
    }
  }
  .views-field-field-img {
    margin: 0 0 20px;
    @media screen and (min-width: 320px) and (max-width: 479px) {
      margin-bottom: 15px;
    }
    .field-content {
      overflow: hidden;
      width: 160px;
      height: 160px;
      border-radius: 50%;
      margin: 0 auto;
      border: 5px solid $color_style;
      @media screen and (min-width: 768px) and (max-width: 979px) {
        border-width: 3px;
        width: 120px;
        height: 120px;
      }
      @media screen and (min-width: 320px) and (max-width: 767px) {
        width: 100px;
        height: 100px;
      }
      img {
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
        display: block;
      }
    }
  }
  .views-field-title {
    margin-bottom: 5px;
    text-align: center;
    .field-content {
      display: block;
      font-weight: 700;
      font-size: 20px;
      font-family: $font_title;
      line-height: 1.2;
      @media screen and (min-width: 480px) and (max-width: 767px) {
        font-size: 16px;
      }
    }
  }
  .views-field-field-post {
    text-align: center;
    margin-bottom: 25px;
    .field-content {
      font-size: 14px;
      line-height: 1.2;
      color: #444;
      text-transform: lowercase;
      @media screen and (min-width: 768px) and (max-width: 979px) {
        font-size: 12px;
      }
      @media screen and (min-width: 320px) and (max-width: 767px) {
        font-size: 12px;
      }
    }
  }
  .views-field-body {
    clear: both;
    box-sizing: border-box;
    position: relative;
    .field-content {
      font-size: 16px;
      font-style: italic;
      @media screen and (min-width: 768px) and (max-width: 979px) {
        font-size: 14px;
      }
      @media screen and (min-width: 480px) and (max-width: 767px) {
        font-size: 12px;
      }
      @media screen and (min-width: 320px) and (max-width: 479px) {
        font-size: 14px;
      }
    }
  }
  .more-link {
    margin-top: 50px;
    text-align: center;
    a {
      @extend %button_big;
    }
  }
}
