
/* Вариант 1 */
@mixin textblock_style_01 {
	background-color: $color_style_bg;
	background-repeat: repeat;
	background-blend-mode: overlay;
	background-image: url(../images/pattern.png) !important;
	@media screen and (min-width: 1180px) {
		background-attachment: fixed;
	}
	@media screen and (min-width: 320px) and (max-width: 479px) {
		margin-bottom: 20px
	}
	.block-content {
		font-size: 0;
	}
	.text {
		display: inline-block;
		text-align: right;
		vertical-align: middle;
		width: 50%;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			width: 100%;
			text-align: center;
		}
	}
	.one {
		font-family: $font_title;
		font-size: 64px;
		font-weight: bold;
		line-height: 50px;
		margin: 0 0 20px;
		@media screen and (min-width: 480px) and (max-width: 979px) {
			font-size: 38px;
			line-height: 42px;
			margin: 0 0 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 767px) {
			text-align: center;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 28px;
			line-height: 36px;
			margin: 0 0 10px;
		}
	}
	.two {
		font-size: 24px;
		font-weight: bold;
		margin: 0;
		@media screen and (min-width: 480px) and (max-width: 979px) {
			font-weight: normal;
			line-height: 30px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			font-size: 18px;
			font-weight: normal;
			line-height: 28px;
		}
	}
	.button {
		display: inline-block;
		vertical-align: middle;
		width: 50%;
		text-align: center;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			width: 100%;
			margin: 40px 0;
		}
		&>a {
			border: 4px solid;
			color: #000;
			font-size: 22px;
			font-weight: bold;
			line-height: 22px;
			margin: 0;
			padding: 12px 30px 10px;
			text-decoration: none;
			text-transform: uppercase;
			letter-spacing: 0.06em;
			transition: all 0.2s ease 0s;
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 18px;
			}
			&:hover, &:focus {
				background: $color_style;
				border-color: $color_style;
				color: #fff;
			}
		}
	}

}
/* конец вариант 1 */

















//=====================================
//=Old
//=====================================
@mixin textblock_style_01_old{
	background-color: #efefef;
	padding: 100px 0;
	@media screen and (min-width: 768px) and (max-width: 1179px) {
		padding: 70px 0;
	}
	@media screen and (min-width: 320px) and (max-width: 767px) {
		padding: 50px 0;
	}
	.field--name-body{
	}
	table{
		width: 100%;
		td{
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 100% !important;
				display: block;
				text-align: center !important;
			}
			&:nth-child(1){
				text-align: right;
				width: 60%;
				@media screen and (min-width: 480px) and (max-width: 979px) {
					width: 50%;
				}
			}
			&:nth-child(2){
				text-align: center;
				width: 40%;
				@media screen and (min-width: 480px) and (max-width: 979px) {
					width: 50%;
				}
			}
		}
		h2{
			@extend %title_h2;
			margin: 0 0 20px;
			font-size: 60px;
			line-height: 1.1;
			@media screen and (min-width: 980px) and (max-width: 1179px) {
				font-size: 50px;
			}
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 40px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 24px;
				margin: 0 0 10px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 30px;
				margin: 0 0 10px;
				line-height: 1.3;
			}
		}
		p{
			font-size: 24px;
			line-height: 1.3;
			@media screen and (min-width: 480px) and (max-width: 979px) {
				font-size: 18px;
				margin: 0;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 18px;
				margin: 0 0 20px;
			}
		}
		a{
			@extend %button_big;
			@media screen and (min-width: 320px) and (max-width: 767px) {
				padding: 12px 25px !important;
				font-size: 16px !important;
			}
		}
	}
}
//=====================================
//=End of Old
//=====================================
