/* Вариант 1 */

@mixin footer_style_01{
	%footer_item {
		width: 26%;
		margin-bottom: 40px;
		@media screen and (min-width: 768px) and (max-width: 979px) {
			width: 29%;
		}		
		@media screen and (min-width: 480px) and (max-width: 767px) {
			width: 29%;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 100%;
			text-align: center;
			margin: 0 auto 25px;
		}
		p, .field-content {
			@media screen and (min-width: 768px) and (max-width: 979px) {
				font-size: 14px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 14px;
				width: 300px;
				margin: 0 auto;
			}
		}
	}
	background-color: #232323;
	.region-footer{
		@extend %layout;
		padding: 50px 0 0;
		color: #fff;
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		@media screen and (min-width: 768px) and (max-width: 1179px)  {
			padding: 50px 20px 0;
		}
		@media screen and (min-width: 480px) and (max-width: 767px)  {
			padding: 30px 10px 0;
			font-size: 12px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px)  {
			padding: 50px 10px 0;
		}		
	}
	.block{
		h2 {
			font-family: $font_title;
			font-size: 20px;
			text-transform: uppercase;
			letter-spacing: 0.06em;
			line-height: 1.1;
			margin: 0 0 20px;
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 16px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				margin: 0 0 5px;
			}
		}
		p{
			margin: 0 0 10px;
			&.telefon{
				font-size: 30px;
				line-height: 1.2;
				font-family: $font_title;
				letter-spacing: 0.06em;
				@media screen and (min-width: 980px) and (max-width: 1179px)  {
					font-size: 24px;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px !important;
				}
				@media screen and (min-width: 480px) and (max-width: 767px)  {
					font-size: 16px;
				}
				@media screen and (min-width: 320px) and (max-width: 479px)  {
					font-size: 18px;
				}	
			}
		}
		.seti{
			display: flex;
			flex-flow: row nowrap;
			justify-content: start;
			a{
				margin: 0 5px 0 0;
				width: 40px;
				height: 40px;
				text-indent: -9999px;
				background-size: 100% auto;
				background-repeat: no-repeat;
				background-position: 0 0;
				@media screen and (min-width: 320px) and (max-width: 767px)  {
					width: 30px;
					height: 30px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					width: 4vw;
					height: 4vw;
				}
				&:hover{
					background-position: 0 bottom;
				}
			}
		}
		a{
			color: $color_style;
			&:hover{
				text-decoration: none;
			}
		}
	}
	#block-4 { //кто мы
		@extend %footer_item;
		.seti {
			display: none;
		}
	}
	#block-5 { //Наши контакты
		@extend %footer_item;
		.adres {
			display: none;
		}
	}
	#block-13 { //Найти нас (на Progress)
		@extend %footer_item;
	}
	#block-12 { //Найти нас (на остальных)
		@extend %footer_item;
	}
	#block-11 { //Найти нас (на остальных)
		@extend %footer_item;
	}
	#block-6 { //производство "Клипсайт"
		width: 100%;
		padding: 70px 0 20px;
		@media screen and (min-width: 320px) and (max-width: 767px) {
			padding: 30px 0 15px;
		}
		table {
			width: 100%;
		}
		td {
			@media screen and (min-width: 480px) and (max-width: 767px) {
				font-size: 14px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 12px;
			}
			&:nth-child(2) {
				text-align: right;
			}
		}
	}
	.soc-seti{
		position: relative;
		z-index: 1;
		font-size: 0;
		a{
			display: inline-block;
			margin: 0 5px 0 0;
			width: 40px;
			height: 40px;
			text-indent: -9999px;
			background-size: 100% auto;
			background-repeat: no-repeat;
			background-position: 0 0;
			@media screen and (min-width: 768px) and (max-width: 979px) {
				width: 30px;
				height: 30px;
			}
			@media screen and (min-width: 480px) and (max-width: 767px) {
				width: 4vw;
				height: 4vw;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 30px;
				height: 30px;
			}
			&:hover{
				background-position: 0 bottom;
			}
		}
	}
	.address {
		margin-bottom: 15px;
	}
}
/* конец вариант 1 */