//Формат даты: <p>d</p><p>F</p><p>Y</p>
@mixin news_style_01 {
	.node__content {
		margin-bottom: 50px;
	}
	.view { 
		@extend %layout;
	}
	.view-content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}
	.views-field {
		text-align: left;
		margin-bottom: 20px;
		@media screen and (min-width: 480px) and (max-width: 767px) {
			text-align: left;
			margin-bottom: 10px;
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			margin-bottom: 10px;
		}
	}
	.views-row {
		width: 100%;
		box-sizing: border-box;
		position: relative;
		margin-bottom: 50px;
		border-bottom: 1px solid #ddd;
		@media screen and (min-width: 480px) {
			padding-left: 130px;			
		}
		@media screen and (min-width: 320px) and (max-width: 479px) {
			padding-left: 0;
			margin-bottom: 30px;
			border: none;
		}
	}
	.views-field-created {
		background: $color_style_bg;		
		font-weight: bold;
		top: 0;
		left: 0;
		padding: 20px 0 12px;
		position: absolute;
		text-align: center;
		width: 100px;
		@media screen and (min-width: 320px) and (max-width: 479px) {
			width: 100%;
			padding: 10px 10px 10px;
			position: static;
			text-align: left;
			box-sizing: border-box;
		}
		p {
			margin: 0;
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 20px !important;
				display: inline-block;
			}
		}
		p:nth-child(1) {
			font-size: 50px;	
			line-height: 1;	
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 40px;
			}	
		}
		p:nth-child(2) {
			font-size: 16px;
			font-weight: bold;
		}
		p:nth-child(3) {
			font-size: 26px;
			font-weight: bold;
			line-height: 1;
		}		
	}
	.views-field-title {
		line-height: 1.3;
		.field-content {
			a {
				color: #000;
				text-decoration: none;
				font-weight: 700;
				font-family: $font_title;
				font-size: 28px;
				border-bottom: 1px solid #000;
				&:hover, &:focus {
					border-color: transparent;
				}
				@media screen and (min-width: 980px) and (max-width: 1179px) {
					font-size: 24px;
				}
				@media screen and (min-width: 768px) and (max-width: 979px) {
					font-size: 20px;
				}
				@media screen and (min-width: 480px) and (max-width: 767px) {
					font-size: 20px;				
				}
				@media screen and (min-width: 320px) and (max-width: 479px) {
					font-size: 18px;
				}
			}
		}
	}
	.views-field-body {
		margin-bottom: 50px;
		.field-content {
			@media screen and (max-width: 979px) {
				font-size: 14px;
			}
		}
		p {
			margin-bottom: 15px;
		}
		br {
			margin-bottom: 15px;
		}
	}
	.views-field-field-cena {
		margin-bottom: 40px;
		.field-content {
			font-size: 24px;
			font-weight: 700;
			font-family: $font_title;
			@media screen and (max-width: 979px) {
				font-size: 20px;
			}
		}
	}
	.more-link {
		text-align: center;
		margin-top: 30px;
		a {
			@extend %button_big;
		}
	}
}

@mixin news_inner_style_01 { //Cтраница новости
	@at-root .sidebar-first & {
	}
	.node__content {
		@extend %layout;
		margin-bottom: 50px;
		&::after {
			content: '';
			display: table;
			clear: both;
		}
		.field--type-image {
			float: right;
			width: 60%;
			margin: 0 0 40px 40px;
			@media screen and (min-width: 480px) and (max-width: 767px) {
				margin: 0 0 20px 20px;
			}
			@media screen and (min-width: 320px) and (max-width: 479px) {
				width: 100%;
				margin-left: 0;
				margin-bottom: 20px;
			}
			img {
				width: 100%;
				height: auto;
			}
		}
		.field--name-body {
			p {
				margin-bottom: 15px;
			}
			br {
				margin-bottom: 15px;
			}
			@media screen and (min-width: 320px) and (max-width: 767px) {
				font-size: 14px;
			}
		}
		.field--name-field-tags {
			color: #444;
			margin-top: 30px;
			@media screen and (min-width: 320px) and (max-width: 479px) {
				font-size: 12px;
			}
			.field__label {
				display: inline-block;
				margin-right: 5px;
			}
			.field__items {
				display: inline-block;
			}
			.field__item {
				display: inline-block;
				a {
					color: inherit;
					text-transform: lowercase;
					&:hover, &:focus {
						color: $color_style;
						text-decoration: none;
					}
				}
			}
		}
	}
}
