
/* Вариант 1 */
@mixin form_style_01{
	background-color: #efefef;
	width: 480px;
	margin: 70px auto;
	padding: 50px 70px;
	@media screen and (max-width: 767px) {
		width: auto;
		padding: 40px;
		margin: 30px 10px;
	}
	@media screen and (max-width: 767px) {
		padding: 40px 5%;
	}
	h2{
		@extend %title_h2;
		margin: 0 0 20px;
		text-align: center;
		line-height: 1;
		@media screen and (max-width: 767px) {
			font-size: 24px;
		}
	}
	.body{
		margin: 0 0 20px;
		@media screen and (max-width: 767px) {
			font-size: 14px;
		}
	}
	.form-item{
		margin: 0 auto 5px;
		label {
			display: block;
			font-weight: 700;
			margin: 10px 0 5px;
		}
		.description {
			color: #777;
			font-size: 14px;
			text-transform: lowercase;
			line-height: 1.2;
			text-align: right;
			margin-top: 5px;
		}
		input[type="text"], input[type="password"], textarea, input[type="email"]{
			box-sizing: border-box;
			width: 100%;
			border: 1px solid #444;
			padding: 15px 25px;
			resize: none;
			@media screen and (max-width: 767px) {
				padding: 12px 20px;
			}
			&::placeholder{
				opacity: 1;
			}
		}
	}
	.form-actions{
		text-align: center;
		margin: 20px 0 0;
		input {
			@extend %button_big
		}
	}
}
/* конец вариант 1 */